.button {
  cursor: pointer;
  /* font: inherit; */
  border: none;
  background-color: #4d1601;
  color: white;
  padding: 0.5rem 1rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-radius: 25px;
  font-weight: bold;
}

.button:hover,
.button:active {
  transform: scale(1.1);
  background-color: #2c0d00;
  box-shadow: -7px -7px 20px 0px rgba(156, 85, 4, 0.555),
    -4px -4px 5px 0px rgba(155, 71, 2, 0.877),
    7px 7px 20px 0px rgba(216, 130, 2, 0.438),
    4px 4px 5px 0px rgba(206, 102, 5, 0.589);
}

.icon {
  width: 1.35rem;
  height: 1.35rem;
  margin-right: 0.5rem;
}

.badge {
  background-color: #a86c12;
  padding: 0.25rem 1rem;
  border-radius: 25px;
  margin-left: 1rem;
  font-weight: bold;
}

.button:hover .badge,
.button:active .badge {
  background-color: #c47b1c;
}

.bump {
  animation: bump 300ms ease-out;
}

@keyframes bump {
  0% {
    transform: scale(1);
  }
  10% {
    transform: scale(0.9);
  }
  30% {
    transform: scale(1.1);
  }
  50% {
    transform: scale(1.15);
  }
  100% {
    transform: scale(1);
  }
}
